import React, { useState, useEffect, Fragment } from "react";
import firebase from "../firebase";

function SnapshotFirebaseAdvanced() {
  const [schools, setSchools] = useState([]);
  const [loading, setLoading] = useState(false);
  const [date, setDate] = useState("");

  const ref = firebase.firestore().collection("schools");

  //REALTIME GET FUNCTION
  function getSchools() {
    setLoading(true);
    ref
      //.where('owner', '==', currentUserId)
      //.where('title', '==', 'School1') // does not need index
      //.where('score', '<=', 10)    // needs index
      //.orderBy('owner', 'asc')
      //.limit(3)
      .onSnapshot((querySnapshot) => {
        const items = [];
        querySnapshot.forEach((doc) => {
          items.push(doc.data());
        });
        setSchools(items);
        setLoading(false);
      });
  }

  useEffect(() => {
    getSchools();

    // eslint-disable-next-line
  }, []);

  //DELETE FUNCTION
  function deleteSchool(school) {
    var dele = prompt("enter secret code to delete");
    if (dele === "jithu123") {
      ref
        .doc(school.id)
        .delete()
        .catch((err) => {
          console.error(err);
        });
    }
  }

  return (
    <Fragment>
      <section className="quiz quiz-small">
        <h2>set date</h2>
        <input
          className="form-input"
          type="date"
          id="start"
          name="trip-start"
          value={date}
          onChange={(e) => setDate(e.target.value)}
        />
      </section>

      {schools.map((school) => {
        if (school.date === date)
          return (
            <section className="quiz quiz-small">
              <div className="school" key={school.id}>
                <h3>Feed</h3>
                <p>Bags Quantity:{school.Bags}</p>
                <p>Feed Cost:{school.feedcost} Rs</p>
                <h3>Seed</h3>
                <p>Seed Quantity:{school.seedquantity}</p>
                <p>Seed Cost: {school.seedcost} Rs</p>
                <h3>Oil</h3>
                <p> Oil: {school.oil} litres</p>
                <p> Oil cost {school.oilcost}</p>
                <p> Watchman Pay : {school.watchmancost} Rs</p>
                <h3>Medicines</h3>
                <p>
                  {school.medicine1}: {school.medicinec1} Rs
                </p>
                <p>
                  {school.medicine2}: {school.medicinec2} Rs
                </p>
                <p>
                  {school.medicine3}: {school.medicinec3} Rs
                </p>
                <p>
                  {school.medicine4}: {school.medicinec4} Rs
                </p>
                <p>
                  {school.medicine5}: {school.medicinec5} Rs
                </p>
                <h3>maintanence and Repair Costs</h3>
                <p>
                  {school.mrwn1}: {school.mrwc1} Rs
                </p>
                <p>
                  {school.mrwn2}: {school.mrwc2} Rs
                </p>
                <p>
                  {school.mrwn3}: {school.mrwc3} Rs
                </p>
                <p>
                  {school.mrwn4}: {school.mrwc4} Rs
                </p>
                <p>
                  {school.mrwn5}: {school.mrwc5} Rs
                </p>

                <p>
                  total:{" "}
                  {school.feedcost +
                    school.seedcost +
                    school.oilcost +
                    school.watchmancost +
                    school.medicinec1 +
                    school.medicinec2 +
                    school.medicinec3 +
                    school.medicinec4 +
                    school.medicinec5 +
                    school.mrwc1 +
                    school.mrwc2 +
                    school.mrwc3 +
                    school.mrwc4 +
                    school.mrwc5}
                </p>
                <p>{school.score}</p>
                <p>{school.ownerEmail}</p>
                <div>
                  <button
                    className="submit-btn"
                    onClick={() => deleteSchool(school)}
                  >
                    Delete Data
                  </button>
                </div>
              </div>
            </section>
          );
      })}
    </Fragment>
  );
}

export default SnapshotFirebaseAdvanced;
