import React, { useState, useEffect, useContext } from "react";
import firebase from "../firebase";
import { v4 as uuidv4 } from "uuid";
import { AuthContext } from "../auth/Auth";

function SnapshotFirebaseAdvanced() {
  const { currentUser } = useContext(AuthContext);

  const [show, setShow] = useState(true);

  const [Bags, setBags] = useState("");
  const [feedcost, setFeedCost] = useState("");
  const [seedquantity, setSeedQuantity] = useState("");
  const [seedcost, setSeedCost] = useState("");
  const [oil, setOilQuantity] = useState("");
  const [oilcost, setOilCost] = useState("");
  const [watchmancost, setWatchmanPay] = useState("");
  const [medicine1, setMedicine1] = useState("");
  const [medicinec1, setMedicineC1] = useState("");
  const [medicine2, setMedicine2] = useState("");
  const [medicinec2, setMedicineC2] = useState("");
  const [medicine3, setMedicine3] = useState("");
  const [medicinec3, setMedicineC3] = useState("");
  const [medicine4, setMedicine4] = useState("");
  const [medicinec4, setMedicineC4] = useState("");
  const [medicine5, setMedicine5] = useState("");
  const [medicinec5, setMedicineC5] = useState("");
  const [mrwn1, setMrwn1] = useState("");
  const [mrwc1, setMrwc1] = useState("");
  const [mrwn2, setMrwn2] = useState("");
  const [mrwc2, setMrwc2] = useState("");
  const [mrwn3, setMrwn3] = useState("");
  const [mrwc3, setMrwc3] = useState("");
  const [mrwn4, setMrwn4] = useState("");
  const [mrwc4, setMrwc4] = useState("");
  const [mrwn5, setMrwn5] = useState("");
  const [mrwc5, setMrwc5] = useState("");

  const [date, setDate] = useState("");

  const ref = firebase.firestore().collection("schools");

  useEffect(() => {
    console.log("hello");

    // eslint-disable-next-line
  }, []);

  // ADD FUNCTION
  function addSchool() {
    const owner = currentUser ? currentUser.uid : "unknown";
    const ownerEmail = currentUser ? currentUser.email : "unknown";
    const newSchool = {
      date,
      Bags: +Bags,
      feedcost: +feedcost,
      seedquantity: +seedquantity,

      seedcost: +seedcost,
      oil: +oil,
      oilcost: +oilcost,
      watchmancost: +watchmancost,
      medicine1: medicine1,
      medicinec1: +medicinec1,
      medicine2: medicine2,
      medicinec2: +medicinec2,
      medicine3: medicine3,
      medicinec3: +medicinec3,
      medicine4: medicine4,
      medicinec4: +medicinec4,
      medicine5: medicine5,
      medicinec5: +medicinec5,
      mrwn1: mrwn1,
      mrwc1: +mrwc1,
      mrwn2: mrwn2,
      mrwc2: +mrwc2,
      mrwn3: mrwn3,
      mrwc3: +mrwc3,
      mrwn4: mrwn4,
      mrwc4: +mrwc4,
      mrwn5: mrwn5,
      mrwc5: +mrwc5,
      id: uuidv4(),
      owner,
      ownerEmail,
      createdAt: firebase.firestore.FieldValue.serverTimestamp(),
      lastUpdate: firebase.firestore.FieldValue.serverTimestamp(),
    };

    ref
      .doc(newSchool.id)
      .set(newSchool)
      .catch((err) => {
        console.error(err);
      });
    setShow(false);
  }

  return (
    <main>
      <section className="quiz quiz-small">
        {show ? (
          <div className="inputBox">
            <div className="form-control">
              <h2>Enter Your Data</h2>
              <h3>Date</h3>
              <input
                className="form-input"
                type="date"
                id="start"
                name="trip-start"
                value={date}
                onChange={(e) => setDate(e.target.value)}
              />

              <h3>Feed</h3>
              <label>no of bags</label>
              <input
                className="form-input"
                type="number"
                value={Bags}
                onChange={(e) => setBags(e.target.value)}
              />
              <label>Cost</label>
              <input
                className="form-input"
                type="number"
                value={feedcost}
                onChange={(e) => setFeedCost(e.target.value)}
              />

              <h3>Seed </h3>
              <label>seed quantity</label>
              <input
                className="form-input"
                type="number"
                value={seedquantity}
                onChange={(e) => setSeedQuantity(e.target.value)}
              />
              <label>seed cost</label>
              <input
                className="form-input"
                type="number"
                value={seedcost}
                onChange={(e) => setSeedCost(e.target.value)}
              />

              <h3>Oil</h3>
              <label>Oil Litres</label>
              <input
                className="form-input"
                type="number"
                value={oil}
                onChange={(e) => setOilQuantity(e.target.value)}
              />
              <label>Oil cost</label>
              <input
                className="form-input"
                type="number"
                value={oilcost}
                onChange={(e) => setOilCost(e.target.value)}
              />
              <label>Watchman cost</label>
              <input
                className="form-input"
                type="number"
                value={watchmancost}
                onChange={(e) => setWatchmanPay(e.target.value)}
              />
              <h3>Medicines</h3>
              <label>Medicine Name 1</label>
              <input
                className="form-input"
                type="text"
                value={medicine1}
                onChange={(e) => setMedicine1(e.target.value)}
              />
              <label>Medicine cost</label>
              <input
                className="form-input"
                type="number"
                value={medicinec1}
                onChange={(e) => setMedicineC1(e.target.value)}
              />
              <label>Medicine Name 2</label>
              <input
                className="form-input"
                type="text"
                value={medicine2}
                onChange={(e) => setMedicine2(e.target.value)}
              />
              <label>Medicine cost</label>
              <input
                className="form-input"
                type="number"
                value={medicinec2}
                onChange={(e) => setMedicineC2(e.target.value)}
              />
              <label>Medicine Name 3</label>
              <input
                className="form-input"
                type="text"
                value={medicine3}
                onChange={(e) => setMedicine3(e.target.value)}
              />
              <label>Medicine cost</label>
              <input
                className="form-input"
                type="number"
                value={medicinec3}
                onChange={(e) => setMedicineC3(e.target.value)}
              />
              <label>Medicine Name 4</label>
              <input
                className="form-input"
                type="text"
                value={medicine4}
                onChange={(e) => setMedicine4(e.target.value)}
              />
              <label>Medicine cost</label>
              <input
                className="form-input"
                type="number"
                value={medicinec4}
                onChange={(e) => setMedicineC4(e.target.value)}
              />
              <label>Medicine Name 5</label>
              <input
                className="form-input"
                type="text"
                value={medicine5}
                onChange={(e) => setMedicine5(e.target.value)}
              />
              <label>Medicine cost</label>
              <input
                className="form-input"
                type="number"
                value={medicinec5}
                onChange={(e) => setMedicineC5(e.target.value)}
              />
              <h3>maintanence and repair costs</h3>
              <label>work name 1</label>
              <input
                className="form-input"
                type="text"
                value={mrwn1}
                onChange={(e) => setMrwn1(e.target.value)}
              />
              <label>work cost</label>
              <input
                className="form-input"
                type="number"
                value={mrwc1}
                onChange={(e) => setMrwc1(e.target.value)}
              />

              <label>work name 2</label>
              <input
                className="form-input"
                type="text"
                value={mrwn2}
                onChange={(e) => setMrwn2(e.target.value)}
              />
              <label>work cost</label>
              <input
                className="form-input"
                type="number"
                value={mrwc2}
                onChange={(e) => setMrwc2(e.target.value)}
              />
              <label>work name 3</label>
              <input
                className="form-input"
                type="text"
                value={mrwn3}
                onChange={(e) => setMrwn3(e.target.value)}
              />
              <label>work cost</label>
              <input
                className="form-input"
                type="number"
                value={mrwc3}
                onChange={(e) => setMrwc3(e.target.value)}
              />
              <label>work name 4</label>
              <input
                className="form-input"
                type="text"
                value={mrwn4}
                onChange={(e) => setMrwn4(e.target.value)}
              />
              <label>work cost</label>
              <input
                className="form-input"
                type="number"
                value={mrwc4}
                onChange={(e) => setMrwc4(e.target.value)}
              />
              <label>work name 5</label>
              <input
                className="form-input"
                type="text"
                value={mrwn5}
                onChange={(e) => setMrwn5(e.target.value)}
              />
              <label>work cost</label>
              <input
                className="form-input"
                type="number"
                value={mrwc5}
                onChange={(e) => setMrwc5(e.target.value)}
              />

              <button className="submit-btn" onClick={() => addSchool()}>
                Submit
              </button>
            </div>
          </div>
        ) : (
          <h3>Data submitted for {date}</h3>
        )}
      </section>
    </main>
  );
}

export default SnapshotFirebaseAdvanced;
